*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --panda-dark-blue: #02050e;
  --panda-light-blue: #1e50ff;
  --pand-first-medium-blue: #0f182e;
  --panda-second-medium-blue: #272d37;
  --panda-grey: #adb9c7;
  --panda-ornament: rgba(30, 80, 255, .5);
  --panda-heart-grey: #e7ecf2;
  --panda-heart-pink: #ff4d6d;
  --panda-first-btn-shadow: 8px 8px 18px 0px rgba(30, 80, 255, .25);
  --panda-first-btn-rgba: rgba(30, 80, 255, .1);
  --panda-second-btn-rgba: rgba(30, 80, 255, .15);
  --panda-second-btn-shadow: 10px 10px 42px 0px rgba(30, 80, 255, .5);
  --panda-third-btn-shadow: 0px 4px 17px rgba(30, 80, 255, .42);
  --panda-form-background-color: rgba(255, 255, 255, .1);
  --panda-form-border-color: #eef2f6;
  --panda-input-grey: #8f9ca9;
  --panda-product-grey: #b8c1cc;
  --clearly-white: #fff;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
}

button {
  color: currentColor;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  font-family: inherit;
}

a {
  color: currentColor;
  text-decoration: none;
}

body {
  color: var(--clearly-white);
  background-color: var(--panda-dark-blue);
  font-family: Poppins, sans-serif;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
  margin: 20px 0;
}

body::-webkit-scrollbar-thumb {
  background-color: var(--panda-light-blue);
  border: 6px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
}

.container {
  width: 1440px;
  margin: 0 auto;
  padding-left: 120px;
  padding-right: 120px;
}

.primary_font {
  letter-spacing: .01em;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
}

.secondary_font {
  letter-spacing: .01em;
  font-size: 16px;
  font-weight: 600;
  line-height: 32px;
}

.text_font_1 {
  letter-spacing: .01em;
  font-size: 16px;
  font-weight: 400;
  line-height: 32px;
}

.text_font_2 {
  letter-spacing: .01em;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.title_font {
  letter-spacing: .01em;
  font-size: 48px;
  font-weight: 700;
  line-height: 64px;
}

.minor_title_font {
  letter-spacing: .01em;
  font-size: 24px;
  font-weight: 700;
  line-height: 32px;
}

.price_font_1 {
  letter-spacing: .01em;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}

.price_font_2 {
  letter-spacing: .01em;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}

.product_small_font_1 {
  letter-spacing: .01em;
  color: var(--panda-product-grey);
  font-size: 12px;
  font-weight: 400;
  line-height: 24px;
}

.product_small_font_2 {
  letter-spacing: .01em;
  color: var(--panda-input-grey);
  font-size: 12px;
  font-weight: 600;
  line-height: 24px;
}

.primary_font, .text_font_1, .text_font_2 {
  color: var(--panda-grey);
}

.title_wrap {
  justify-content: space-between;
  margin-bottom: 56px;
  display: flex;
}

.current {
  color: var(--clearly-white);
}

.current:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--panda-light-blue);
  border-radius: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.primary_button {
  min-width: 175px;
  border: 1px solid var(--panda-light-blue);
  color: var(--clearly-white);
  border-radius: 16px;
  padding: 12px 16px;
  transition: transform .5s ease-in-out;
}

.primary_button:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.secondary_button {
  min-width: 119px;
  color: var(--clearly-white);
  background-color: var(--panda-first-btn-rgba);
  border-radius: 8px;
  padding: 16px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.secondary_button:hover {
  cursor: pointer;
  background-color: var(--panda-light-blue);
  box-shadow: var(--panda-second-btn-shadow);
}

.secondary_button--active {
  background-color: var(--panda-light-blue);
  box-shadow: var(--panda-second-btn-shadow);
}

.section {
  padding-bottom: 160px;
}

.logo {
  color: var(--clearly-white);
  align-items: center;
  font-family: Poppins, sans-serif;
  transition: transform .5s ease-in-out;
  display: flex;
}

.logo:hover {
  transform: scale(1.1);
}

.logo__image {
  width: 43px;
  height: 40px;
  margin-right: 10px;
}

.ornament {
  z-index: -1;
  width: 638px;
  height: 587px;
  background: var(--panda-ornament);
  filter: blur(350px);
  position: absolute;
  overflow: hidden;
}

.button_to_top {
  z-index: 1;
  width: 50px;
  height: 50px;
  opacity: 0;
  visibility: hidden;
  border: 1px solid var(--panda-light-blue);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 20%;
  justify-content: center;
  align-items: center;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  bottom: 3vh;
  right: -140px;
}

.button_to_top:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.button_to_top--visible {
  opacity: 1;
  visibility: visible;
  right: 2.5%;
}

.visually_hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is_hidden {
  pointer-events: none;
  visibility: hidden;
  display: none;
}

.bid_list {
  column-gap: 24px;
  display: flex;
}

.bid_item {
  background-color: var(--panda-second-medium-blue);
  border-radius: 16px;
  padding: 24px;
  transition: transform .5s ease-in-out;
}

.bid_item:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.bid_image_wrap {
  position: relative;
}

.bid_image {
  margin-bottom: 24px;
}

.bid_icon_wrap {
  width: 40px;
  height: 40px;
  background-color: var(--clearly-white);
  border-radius: 20px;
  padding: 8px;
  position: absolute;
  top: 24px;
  right: 24px;
}

.bid_icon {
  color: var(--panda-heart-grey);
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.bid_icon:hover, .bid_icon--pink {
  color: var(--panda-heart-pink);
}

.bid_name_price_wrap {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.bid_name, .bid_pre_price {
  margin-bottom: 8px;
}

.bid_author {
  color: var(--clearly-white);
  align-items: center;
  display: flex;
}

.bid_author:before {
  content: "";
  width: 40px;
  height: 40px;
  background-image: url("artist_mini.b0f00a18.png");
  margin-right: 8px;
}

.bid_price {
  text-align: right;
}

.bid_time_wrap {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.bid_time {
  align-items: center;
  display: flex;
}

.bid_time svg {
  margin-right: 8px;
}

.bid_button {
  min-width: 129px;
  color: var(--clearly-white);
  background-color: var(--panda-light-blue);
  box-shadow: var(--panda-first-btn-shadow);
  border-radius: 16px;
  padding: 8px 16px;
  transition: transform .5s ease-in-out;
}

.bid_button:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.pagination {
  max-width: 371px;
  margin: 0 auto;
}

.pagination__wrap {
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.pagination__prev_button, .pagination__next_button {
  width: 32px;
  height: 32px;
  background-color: var(--panda-product-grey);
  border-radius: 16px;
  padding: 8px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.pagination__prev_button:hover, .pagination__next_button:hover {
  cursor: pointer;
  background-color: var(--panda-light-blue);
}

.pagination__prev_button:hover .pagination__icon, .pagination__next_button:hover .pagination__icon {
  color: var(--clearly-white);
}

.pagination__icon {
  color: var(--pand-first-medium-blue);
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.pagination__list {
  align-items: center;
  column-gap: 20px;
  margin-left: 20px;
  margin-right: 20px;
  display: flex;
}

.pagination__item--active {
  width: 32px;
  height: 32px;
  background-color: var(--panda-light-blue);
  filter: drop-shadow(var(--panda-third-btn-shadow) );
  border-radius: 16px;
  padding: 4px 12px;
  transition: transform .5s ease-in-out;
}

.pagination__item--active:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.pagination__item--active:hover .pagination__page_button {
  color: var(--clearly-white);
}

.pagination__page_button {
  color: var(--clearly-white);
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.pagination__page_button:hover {
  cursor: pointer;
  color: var(--panda-light-blue);
}

.pagination_navigation_image {
  margin: 0 auto;
}

.header {
  padding-top: 40px;
  padding-bottom: 40px;
}

.header .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav__list {
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__item:not(:last-child) {
  margin-right: 32px;
}

.nav__item p {
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.nav__item p:hover {
  cursor: pointer;
  color: var(--panda-light-blue);
}

.main__box {
  padding-top: 56px;
  padding-bottom: 160px;
}

.discover {
  padding-bottom: 120px;
}

.discover .container {
  justify-content: center;
  align-items: center;
  margin-bottom: 56px;
  display: flex;
}

.main__title {
  letter-spacing: .01em;
  max-width: 682px;
  margin-right: 24px;
  font-size: 64px;
  font-weight: 700;
  line-height: 80px;
}

.main__after_title_wrap {
  max-width: 488px;
}

.main__after_title {
  margin-bottom: 24px;
}

.main__button {
  color: var(--clearly-white);
  align-items: center;
  text-decoration: underline;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.main__button:hover {
  cursor: pointer;
  color: var(--panda-light-blue);
}

.main__arrow {
  fill: currentColor;
  margin-left: 8px;
}

.splide {
  margin: 0 auto;
}

.splide__slide:hover {
  cursor: pointer;
}

.partners {
  padding-bottom: 151px;
  position: relative;
}

.partners__list {
  justify-content: center;
  align-items: center;
  display: flex;
}

.partners__item {
  transition: transform .5s ease-in-out;
}

.partners__item:not(:last-child) {
  margin-right: 41px;
}

.partners__item:hover {
  transform: scale(1.1);
}

.partners__ornament {
  top: -100%;
  right: 0;
}

.create_sell__title {
  max-width: 583px;
  text-align: center;
  margin: 0 auto 56px;
}

.create_sell__list {
  align-items: center;
  display: flex;
}

.create_sell__item {
  background-color: var(--panda-second-medium-blue);
  text-align: center;
  border-radius: 16px;
  padding: 56px 42px 56px 32px;
  transition: transform .5s ease-in-out;
}

.create_sell__item:not(:last-child) {
  margin-right: 30px;
}

.create_sell__item:nth-child(2) {
  border: 1px solid var(--clearly-white);
  background-color: rgba(0, 0, 0, 0);
}

.create_sell__item:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.create_sell__icon_wrap {
  width: 80px;
  height: 80px;
  background-color: var(--panda-light-blue);
  border-radius: 40px;
  margin: 0 auto 48px;
  padding: 23px;
}

.create_sell__minor_title {
  margin-bottom: 24px;
}

.live_actions {
  position: relative;
}

.live_actions__ornament {
  top: 50%;
  left: -30%;
}

.featured_artist__list {
  justify-content: center;
  display: flex;
}

.featured_artist__item {
  text-align: center;
  transition: transform .5s ease-in-out;
}

.featured_artist__item:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.featured_artist__item:nth-child(2n) {
  flex-grow: 1;
}

.featured_artist__item:not(:last-child) {
  margin-right: 40px;
}

.featured_artist__item .minor_title_font {
  margin-bottom: 16px;
}

.featured_artist__item .price_font_2 {
  justify-content: center;
  align-items: center;
  display: flex;
}

.featured_artist__item .price_font_2 svg {
  margin-right: 8px;
}

.featured_artist__image_wrap {
  max-width: 120px;
  justify-content: center;
  margin: 0 auto 17px;
  display: flex;
  position: relative;
}

.featured_artist__check_icon {
  position: absolute;
  bottom: 0;
  right: 0;
}

.hot_nft {
  margin-bottom: 56px;
  position: relative;
}

.hot_nft h2 {
  text-align: center;
  margin-bottom: 56px;
}

.hot_nft__button_list {
  justify-content: center;
  margin-bottom: 56px;
  display: flex;
}

.hot_nft__button_item:not(:last-child) {
  margin-right: 24px;
}

.hot_nft__bid_list {
  flex-wrap: wrap;
  row-gap: 56px;
}

.hot_nft__bid_item {
  transition: transform .5s ease-in-out;
}

.hot_nft__bid_item:hover {
  cursor: pointer;
  transform: scale(1.05);
}

.hot_nft__ornament {
  bottom: 5%;
  right: 5%;
}

.join_comunity__wrap {
  text-align: center;
  background-color: var(--panda-light-blue);
  border-radius: 16px;
  padding: 48px 205px;
}

.join_comunity__wrap .title_font {
  margin-bottom: 24px;
}

.join_comunity__wrap .text_font_1 {
  color: var(--clearly-white);
  margin-bottom: 40px;
}

.join_comunity__button {
  min-width: 239px;
  color: var(--panda-light-blue);
  background-color: var(--clearly-white);
  border-radius: 16px;
  padding: 16px;
  transition: transform .5s ease-in-out;
}

.join_comunity__button:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.collectibles__box {
  padding-top: 80px;
  padding-bottom: 160px;
}

.collectibles .title_font {
  text-align: center;
  margin-bottom: 56px;
}

.collectibles__form {
  max-width: 800px;
  border: 1px solid var(--panda-form-border-color);
  background-color: var(--panda-form-background-color);
  border-radius: 16px;
  align-items: center;
  margin: 0 auto 56px;
  padding: 16px;
  display: flex;
}

.collectibles__input {
  width: 100%;
  letter-spacing: .01em;
  color: var(--clearly-white);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  margin-right: 8px;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
}

.collectibles__input::-ms-input-placeholder {
  color: var(--panda-input-grey);
}

.collectibles__input::placeholder {
  color: var(--panda-input-grey);
}

.collectibles__form_button {
  width: 40px;
  height: 40px;
  background-color: var(--panda-light-blue);
  border-radius: 20px;
  padding: 12px;
  transition: transform .5s ease-in-out;
}

.collectibles__form_button:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.collectibles__button_list {
  display: flex;
}

.collectibles__button_item:nth-child(1) {
  margin-right: auto;
}

.collectibles__button_item:nth-child(1) .secondary_button {
  min-width: 175px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.collectibles__button_item:not(:first-child) {
  margin-left: 24px;
}

.collectibles__button_list, .collectibles__bid_list {
  margin-bottom: 56px;
}

.collectibles__bid_list {
  flex-wrap: wrap;
  row-gap: 32px;
}

.collectibles__more_button {
  border-radius: 8px;
  margin: 0 auto;
  padding: 16px;
  display: block;
}

.product__box {
  padding-top: 48px;
  padding-bottom: 160px;
}

.product__main {
  margin-bottom: 72px;
}

.product__main .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.product__image_wrap {
  position: relative;
}

.product__time_wrap {
  background-color: var(--pand-first-medium-blue);
  border-radius: 16px;
  padding: 16px 24px;
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(-60%, -25%);
}

.product__time {
  align-items: center;
  display: flex;
}

.product__time svg {
  margin-right: 16px;
}

.product__main_description {
  max-width: 575px;
}

.product__title, .product__after_title {
  margin-bottom: 24px;
}

.product__main_price_list, .product__main_artist_list, .product__main_button_list {
  justify-content: space-between;
  display: flex;
}

.product__main_price_list {
  margin-bottom: 40px;
}

.product__main_price_item:nth-child(2) {
  text-align: right;
}

.product__main_price_item .text_font_1 {
  margin-bottom: 8px;
}

.product__main_artist_list {
  background-color: var(--panda-second-medium-blue);
  border-radius: 16px;
  margin-bottom: 56px;
  padding: 18px 24px;
}

.product__main_artist_item {
  align-items: center;
  display: flex;
}

.product__main_artist_item img {
  margin-right: 16px;
}

.product__main_artist_item .text_font_1 {
  margin-bottom: 4px;
}

.product__main_button {
  min-width: 275px;
  background-color: var(--panda-light-blue);
}

.product__main_button--active {
  border: 1.5px solid var(--panda-light-blue);
  background: var(--panda-second-btn-rgba);
}

.product__more_button {
  min-width: 176px;
  border: 2px solid var(--panda-light-blue);
  text-align: center;
}

.product__main_button, .product__more_button {
  border-radius: 8px;
  padding: 16px;
  transition: transform .5s ease-in-out;
}

.product__main_button:hover, .product__more_button:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.product__minor_button_list {
  justify-content: space-around;
  margin-bottom: 40px;
  display: flex;
}

.product__minor_button {
  min-width: 175px;
