.main__box {
	padding-top: 56px;
	padding-bottom: 160px;
}

.discover {
	padding-bottom: 120px;
	.container {
		@include align_list;
		justify-content: center;
		margin-bottom: 56px;
	}
}

.main__title {
	@include font($fs:64px, $fw:700, $lh:80px);
	margin-right: 24px;
	max-width: 682px;
}

.main__after_title_wrap {
	max-width: 488px;
}

.main__after_title {
	margin-bottom: 24px;
}

.main__button {
	@include align_list;
	text-decoration: underline;
	color: var(--clearly-white);
	@include change_color_with_pointer;
}

.main__arrow {
	margin-left: 8px;
	fill: currentColor;
}

